<template>
  <div class="zhibei">{{angle}}</div>
</template>

<script>
import * as util from "../assets/util";

export default {
  name: 'compass',
  data () {
    return {
      getIt: false,
      angle: 0
    }
  },
  methods: {
    drawGradienterCircles: function(angle) {
      if (this.getIt) {
        return
      }

      if (angle < .2) {
        angle = 0
      } else if (angle > 1) {
        angle = parseInt(angle)
      } else {
        angle = 1
      }
      //this.angle = angle;
    },
    deviceOrientationListener: function(event){
      this.angle = event.gamma;
      let gamma = event.gamma / 360 * Math.PI
      let beta = event.beta / 360 * Math.PI
      let sinGamma = Math.sin(gamma)
      let cosGamma = Math.cos(gamma)
      let sinBeta = Math.sin(beta)
      let cosBeta = Math.cos(beta)
      let angle = Math.acos(cosGamma * cosBeta/(Math.sqrt(Math.pow(cosBeta * sinGamma, 2) + Math.pow(sinBeta, 2) + Math.pow(cosBeta * cosGamma, 2))))
      this.drawGradienterCircles(angle / Math.PI * 360)
      // gradienter end
    }
  },
  created: function(){
    if (window.DeviceOrientationEvent) {
      let vm = this;
      window.addEventListener('deviceorientation', util.throttle(vm.deviceOrientationListener, 10, 30))
    } else {
      alert("Sorry your browser doesn't support Device Orientation")
    }
  },
  mounted: function(){
    
  }
}
</script>

<style scoped>
.zhibei{position: fixed;left: .5em;top:.5em; width: 3em;height: 3em;background: url(../assets/img/zhibei.png) center no-repeat rgba(255,255,255,.7);background-size: contain;z-index: 9;
border:1px solid #f5f5f5; box-shadow: 1px 1px 1px #eee;border-radius: 50%;overflow:hidden;pointer-events: none;}

